<template>
    <div class="product__counter form__counter">
        <!-- <button type="button" @click="$emit('change', amount - 1)" aria-label="Убрать один товар"> -->
        <button type="button" @click.prevent="change(amount)" aria-label="Убрать один товар">
            <svg width="10" height="10" fill="currentColor">
                <use xlink:href="#icon-minus"></use>
            </svg>
        </button>

        <input type="text" :value="amount" @input="$emit('change', $event.target.value)" name="count">

        <button type="button" @click="$emit('change', amount + 1)" aria-label="Добавить один товар">
            <svg width="10" height="10" fill="currentColor">
                <use xlink:href="#icon-plus"></use>
            </svg>
        </button>
    </div>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
export default {
    model: {
        prop: 'amount',
        event: 'change',
    },
    props: ['amount'],
    emits: ['update:change'],
    filters: { numberFormat },
    methods: {
        change(amount) {
            if (amount <= 1) {
                amount = 1
            } else {
                this.$emit('change', this.amount - 1)
            }
        }
    }
}
</script>